.tree {
  font-size: 1.2rem;
}

.tree__html-object {
  height: 1px;
  width: 1px;
}

.tree__html-label {
  width: 100px;
  text-align: center;
  margin: 0;
  color: var(--thl-ui-text);
  font-weight: bold;
}

.tree__node-marker {
  fill: #fff;
  stroke: steelblue;
  stroke-width: 3px;
  cursor: pointer;
}

.tree__node-label {
  fill: var(--thl-ui-text);
  font-weight: bold;
}

.tree__url-link > * {
  fill: var(--thl-link);
}

.tree__url-link > *:hover {
  text-decoration: underline;
}

.tree__link {
  fill: none;
  stroke: var(--thl-gray-1);
  stroke-width: 2px;
}
